/* Start of Homepage Grid*/
.dg_icon-link {
  margin: 10px 0;
}

.dg_item-grid {
  display: flex;
  flex-wrap: wrap;
  font-size: 18px;
}

.dg_item-grid > * {
  padding: 15px 10px;
  margin-right: 2%;
  margin-bottom: 1%;
  width: 18.4%;
}

.dg_item-grid > *:nth-child(5n) {
  margin-right: 0;
}

@media screen and (max-width: 1100px) {
  .dg_item-grid > * {
    margin-right: 2%;
    margin-bottom: 1%;
    width: 32%;
  }

  .dg_item-grid > *:nth-child(5n) {
    margin-right: 2%;
  }

  .dg_item-grid > *:nth-child(3n) {
    margin-right: 0;
  }

  .dg_item-grid > *:last-child {
    margin-right: 0;
    margin-bottom: 1%;
    width: 100%;
  }
}

/* End of Homepage Grid*/

.dg_main-content .dg_item-grid > * {
  margin-right: 2%;
  margin-bottom: 1%;
  width: 32%;
}

.dg_main-content .dg_item-grid > *:nth-child(5n) {
  margin-right: 2%;
}

.dg_main-content .dg_item-grid > *:nth-child(3n) {
  margin-right: 0;
}

@media screen and (max-width: 768px) {
  .dg_item-grid > * {
    margin-right: 0;
    margin-bottom: 1%;
    width: 100%;
  }

  .dg_item-grid > *:nth-child(5n) {
    margin-right: 0%;
  }

  .dg_main-content .dg_item-grid > * {
    margin-right: 2%;
    margin-bottom: 1%;
    width: 48%;
  }

  .dg_main-content .dg_item-grid > *:nth-child(3n) {
    margin-right: 2% !important;
  }
}

@media screen and (max-width: 576px) {
  .dg_icon-link {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    text-align: left;
    padding: 10px;
  }

  .dg_icon-link.dg_icon-link--large i {
    padding: 10px 20px 10px 10px;
    line-height: 1;
    height: auto;
    min-width: 75px;
    text-align: center;
    width: auto;
  }

  .dg_main-content .dg_item-grid > * {
    margin-right: 0;
    margin-bottom: 1%;
    width: 100%;
  }

  .dg_main-content .dg_item-grid > *:nth-child(5n),
  .dg_main-content .dg_item-grid > *:nth-child(3n) {
    margin-right: 0% !important;
  }
}
